export type YomtorColor = {
    color?: YomtorPaletteColor
    name?: string
}

function addLightOrDark(
    intent: YomtorPaletteColor,
    direction: keyof YomtorPaletteColor,
    shade: number,
    tonalOffset: any
) {
    const tonalOffsetLight = tonalOffset.light || tonalOffset
    const tonalOffsetDark = tonalOffset.dark || tonalOffset * 1.5

    if (!intent[direction]) {
        if (Object.prototype.hasOwnProperty.call(intent, shade)) {
            intent[direction] =
                intent[shade as unknown as keyof YomtorPaletteColor]
        } else if (direction === 'lightest') {
            intent.lightest = lighten(intent.main, 0.8)
        } else if (direction === 'light') {
            intent.light = lighten(intent.main, tonalOffsetLight)
        } else if (direction === 'dark') {
            intent.dark = darken(intent.main, tonalOffsetDark)
        } else if (direction === 'darkest') {
            intent.darkest = darken(intent.main, 0.8)
        }
    }
}

export const getContrastText = (background: string, contrastThreshold = 3) => {
    const contrastText =
        getContrastRatio(background, dark.text?.main || '') >= contrastThreshold
            ? dark.text?.main
            : light.text?.main

    return contrastText
}

export const augmentColor = (
    { color, mainShade = 500, lightShade = 300, darkShade = 700 }: any,
    tonalOffset = 0.2,
    contrastThreshold = 3
): YomtorPaletteColor => {
    color = isString(color) ? { main: color } : { ...color }
    if (!color.main && color[mainShade]) {
        color.main = color[mainShade]
    }

    addLightOrDark(color, 'lightest', lightShade, tonalOffset)
    addLightOrDark(color, 'light', lightShade, tonalOffset)
    addLightOrDark(color, 'dark', darkShade, tonalOffset)
    addLightOrDark(color, 'darkest', darkShade, tonalOffset)

    if (!color.text && dark && light) {
        color.text = getContrastText(color.main, contrastThreshold)
    }

    return color
}

