export default function createPalette(palette: YomtorPalette): YomtorPalette {
    const {
        primary = {
            lightest: '#D9ECD9',
            light: green[80],
            main: green[100],
            dark: darken(green[100])
        },
        secondary = {
            light: grey[60],
            main: grey[80],
            dark: grey[100]
        },
        error = {
            light: red[80],
            main: red[100],
            dark: darken(red[100])
        },
        warning = {
            light: orange[80],
            main: orange[100],
            dark: darken(orange[100]),
            text: 'white'
        },
        info = {
            light: blue[80],
            main: blue[100],
            dark: darken(blue[100])
        },
        success = {
            light: green[10],
            main: green[80],
            dark: green[100]
        },
        white = common.white,
        black = common.black,
        mode = 'light',
        contrastThreshold = 3,
        tonalOffset = 0.2,
        ...other
    } = palette

    const modes = { dark, light }

    const paletteOutput = mergeObjects(
        {
            common,
            mode,
            primary: augmentColor(
                { color: primary },
                tonalOffset,
                contrastThreshold
            ),
            secondary: augmentColor(
                { color: secondary },
                tonalOffset,
                contrastThreshold
            ),
            error: augmentColor(
                { color: error },
                tonalOffset,
                contrastThreshold
            ),
            warning: augmentColor(
                { color: warning },
                tonalOffset,
                contrastThreshold
            ),
            info: augmentColor({ color: info }, tonalOffset, contrastThreshold),
            success: augmentColor(
                { color: success },
                tonalOffset,
                contrastThreshold
            ),
            grey,
            contrastThreshold,
            getContrastText,
            augmentColor,
            tonalOffset
        },
        modes[mode],
        other
    )

    return paletteOutput
}
